body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', 'Arial', sans-serif;
  background-color: #fff; }

img {
  max-width: 100%;
  height: auto; }

.button {
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 48px;
  font-weight: 700;
  display: block;
  box-sizing: border-box;
  border: 2px solid #444444;
  color: #444444;
  background-color: transparent;
  text-align: center;
  vertical-align: middle;
  text-decoration: none;
  text-transform: uppercase;
  height: 52px; }
  .button:hover {
    cursor: pointer;
    background-color: #444444;
    color: #fff; }
  .button:active {
    background-color: #444444;
    color: rgba(255, 255, 255, 0.3); }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.zigzag {
  margin: 0 auto;
  height: 6px;
  width: 320px;
  background: url("../img/bg-zigzag-line.svg") repeat-x;
  background-size: 100% 6px; }

.container {
  position: relative;
  padding-right: 30px;
  padding-left: 30px;
  margin: 0 auto; }

.inner-container {
  margin: 0 auto; }

iframe {
  min-width: 320px;
  width: 100%;
  border: none; }

.inner-page__title {
  margin: 0;
  padding: 0;
  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  padding-top: 70px;
  padding-bottom: 76px;
  background-color: #f1f1f1;
  text-align: center;
  margin-bottom: 30px; }

@media (max-width: 768px) {
  .container {
    max-width: 260px; }
  .inner-container {
    max-width: 320px; } }

@media (min-width: 768px) {
  .container {
    width: 668px;
    padding-right: 50px;
    padding-left: 50px; }
  .inner-container {
    width: 768px; }
  .inner-page__title {
    font-size: 43px;
    line-height: 50px;
    font-weight: 700;
    padding-top: 90px;
    padding-bottom: 93px; }
  .decoration-wrapper {
    position: relative; }
  .decoration {
    position: absolute;
    margin-top: -14px;
    min-height: 24px;
    min-width: 100%;
    background: url("../img/bg-zigzag-fill.svg") repeat-x 0 0px;
    background-size: auto 24px; }
  .decoration--iner-page {
    top: -26px; }
  .zigzag {
    height: 16px;
    width: 768px;
    background-size: 100% 16px; } }

@media (min-width: 1200px) {
