    width: 1050px;
    padding-right: 0px;
    padding-left: 0px; }
  .inner-container {
    width: 1050px; }
  .inner-page__title {
    font-size: 53px;
    line-height: 57px;
    font-weight: 700;
    padding-top: 94px;
    padding-bottom: 103px; }
  .decoration {
    position: absolute;
    margin-top: -14px;
    min-height: 24px;
    min-width: 100%;
